import React, { useState, useEffect } from "react";
import { useTranslation } from "react-i18next";

const CookiesPolicy = () => {
  const { t } = useTranslation(); // استخدام الترجمة
  const [accepted, setAccepted] = useState(false);

  // التحقق من حالة الكوكيز عند تحميل الصفحة
  useEffect(() => {
    const cookiesAccepted = localStorage.getItem("cookiesAccepted");
    if (cookiesAccepted === "true") {
      setAccepted(true);
    }
  }, []);

  // تحديث العنوان و meta tags عند تغيير اللغة
  useEffect(() => {
    document.title = t("cookiesPolicy.policyTitle");

    let metaDescription = document.querySelector('meta[name="description"]');
    if (!metaDescription) {
      metaDescription = document.createElement("meta");
      metaDescription.name = "description";
      document.head.appendChild(metaDescription);
    }
    metaDescription.setAttribute("content", t("cookiesPolicy.description"));

    let metaKeywords = document.querySelector('meta[name="keywords"]');
    if (!metaKeywords) {
      metaKeywords = document.createElement("meta");
      metaKeywords.name = "keywords";
      document.head.appendChild(metaKeywords);
    }
    metaKeywords.setAttribute("content", t("cookiesPolicy.keywords"));
  }, [t]);

  // دالة قبول سياسة الكوكيز
  const handleAccept = () => {
    setAccepted(true);
    localStorage.setItem("cookiesAccepted", "true"); // حفظ القبول في localStorage
  };

  // إذا كان المستخدم قد قبل سياسة الكوكيز، نعرض النص
  if (accepted) {
    return null; // يمكنك إخفاء سياسة الكوكيز إذا تم قبولها
  }

  return (
    <div className="cookies-policy-container">
      <div className="cookies-policy-header">
        <h1>{t("cookiesPolicy.policyTitle")}</h1>
      </div>
      <div className="cookies-policy-body">
        <section>
          <h2>{t("cookiesPolicy.introduction")}</h2>
        </section>

        <section>
          <h2>{t("cookiesPolicy.typesTitle")}</h2>
          <ul>
            <li>
              <strong>{t("cookiesPolicy.essential")}</strong>
            </li>
            <li>
              <strong>{t("cookiesPolicy.analytical")}</strong>
            </li>
            <li>
              <strong>{t("cookiesPolicy.functional")}</strong>
            </li>
            <li>
              <strong>{t("cookiesPolicy.advertising")}</strong>
            </li>
          </ul>
        </section>

        <section>
          <h2>{t("cookiesPolicy.control")}</h2>
          <p>{t("cookiesPolicy.controlExplanation")}</p>
        </section>

        <section>
          <h2>{t("cookiesPolicy.contact")}</h2>
          <p>
            <strong>G. GROUP S.R.L.</strong>
            <br />
            {t("cookiesPolicy.address")}
            <br />
            {t("cookiesPolicy.phone")}: [+39 XXXXXXXX]
            <br />
            {t("cookiesPolicy.email")}: [info@ggroup.com]
          </p>
        </section>

        {/* زر قبول سياسة الكوكيز */}
        <section>
          <button className="btn btn-primary" onClick={handleAccept}>
            {t("cookiesPolicy.accept")}
          </button>
        </section>
      </div>
    </div>
  );
};

export default CookiesPolicy;
